import React, { Component } from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardTitle, CardActions, Button, CardMenu, IconButton, CardText} from 'react-mdl';

class Projects extends Component {
    state = { activeTab: 0 };
    toggleCategories(){
        if (this.state.activeTab === 0){
            return (
                <Grid className="projects-grid">
                <Card shadow={5} style={{minwidth: '450px', margin: 'auto'}}>
                    <CardTitle style={{color: 'white', height: '176px', background: 'url(data:image/png;base64,)', backgroundSize: 'cover'}}>React Project 1</CardTitle>
                    <CardText>This is the first React project</CardText>
                    <CardActions border>
                        <Button colored>GitHub</Button>
                        <Button colored>Live DEMO</Button>
                    </CardActions>
                    <CardMenu style={{color: 'white'}}>
                        <IconButton name='share'/>
                    </CardMenu>
                </Card>

                <Card shadow={5} style={{minwidth: '450px', margin: 'auto'}}>
                   <CardTitle style={{color: 'white', height: '176px', background: 'url(data:image/png;base64,)', backgroundSize: 'cover'}}>React Project 2</CardTitle>
                   <CardText>This is the second React project</CardText>
                   <CardActions border>
                       <Button colored>GitHub</Button>
                       <Button colored>Live DEMO</Button>
                   </CardActions>
                   <CardMenu style={{color: 'white'}}>
                       <IconButton name='share'/>
                   </CardMenu>
               </Card>

               <Card shadow={5} style={{minwidth: '450px', margin: 'auto'}}>
                   <CardTitle style={{color: 'white', height: '176px', background: 'url(data:image/png;base64,)', backgroundSize: 'cover'}}>React Project 3</CardTitle>
                   <CardText>This is the third React project</CardText>
                   <CardActions border>
                       <Button colored>GitHub</Button>
                       <Button colored>Live DEMO</Button>
                   </CardActions>
                   <CardMenu style={{color: 'white'}}>
                       <IconButton name='share'/>
                   </CardMenu>
               </Card>
               </Grid>
            )
        } else if (this.state.activeTab === 1){
            return (
                <div><h1>This is Vanilla JavaScript</h1></div>
        )} else if (this.state.activeTab === 2){
            return (
                <div><h1>This is HTML/CSS</h1></div>
        )}  else if (this.state.activeTab === 3){
            return (
                <div><h1>This is Other</h1></div>
        )}


    }
    
    render() { 
        return ( 
            <div className='main-page category-tabs'>
                <h1>Projects</h1>
                <Tabs activeTab={this.state.activeTab} onChange={(tabId)=>this.setState({activeTab: tabId})} ripple>
                    <Tab>React</Tab>
                    <Tab>Vanilla JavaScript</Tab>
                    <Tab>HTML/CSS</Tab>
                    <Tab>Other</Tab>
                </Tabs>
                <Grid>
                    <Cell col={12}>
                         <div className="content">{this.toggleCategories()}</div>
                    </Cell>
                </Grid>
                
                
            </div>
         );
    }
};
 
export default Projects;


